import React from 'react'
import './index.less'
import {Button, Icon, message, Modal, Spin} from 'antd';
import axios from 'axios'
import {CopyToClipboard} from 'react-copy-to-clipboard';
import {baseUrl} from '../../../config';
import html2canvas from 'html2canvas';
import Zmage from 'react-zmage'

var QRCode = require('qrcode.react');
message.config({
    top: 500,
    duration: 2,
    maxCount: 2,
});
export default class Product extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products: [],
            display_name: 'none',
            display_back: 'block',

        }
    }

    componentWillMount() {
        console.log('pppppp', this.props);
        axios.get(
            baseUrl + '/api/get_products',
            {
                params: {
                    id_seller: this.props.match.params.id,
                    order: 'id',
                    last_product: this.props.match.params.pid - 1,
                    order_value: this.props.match.params.pid - 1,
                    esc: 'true'
                }
            })
            .then((Response) => {
                this.setState({
                    products: Response.data.products,
                });
                console.table('ttt', this.state)
            })
            .catch((err) => {
                console.log(err);
            })
    }

    renderButtonShit = (us) => {
        var ls = [];
        us.map(u => {
            if (u.content_type === "taok") {
                ls.push(
                    < CopyToClipboard text={u.content}
                                      onCopy={this.onCopy}>
                        <Button className='button-copy' onClick={this.infoCopied}>
                            <Icon type="copy"/>淘口令
                        </Button>
                    </CopyToClipboard>
                )
            } else {
                ls.push(
                    <a href={u.content.includes('http://') || u.content.includes('https://') ? u.content : `https://${u.content}`}>
                        <Button className='button-copy'>
                            <Icon type="link"/>{this.type_convert(u.content_type)}
                        </Button>
                    </a>
                )
            }
        });
        return ls
    };
    type_convert = (type) => {
        switch (type) {
            case "taok":
                return "淘口令";
            case "pdd":
                return "拼多多";
            case "tburl":
                return "淘宝链接";
            case "jd":
                return "京东链接";
            default:
                return "淘宝链接";
        }
    };
    goBack = () => {
        document.location = `#/seller/${this.props.match.params.id}`
    };
    share = () => {
        this.setState({
            display_name: 'block',
            display_back: 'none',
        });
        setTimeout(() => {

            html2canvas(document.querySelector(".capture"), {
                useCORS: true,// 允许跨域
                tainttest: true,
            }).then(canvas => {
                // document.body.appendChild(canvas)
                console.log(canvas.toDataURL());
                Modal.success({
                    title: '长按保存或转发',
                    content: <img src={canvas.toDataURL()} alt='' width='100%'/>,
                });
                this.setState({
                    display_name: 'none',
                    display_back: 'block',
                })
            });
        }, 10);

    };

    render() {
        return (

            this.state.products[0]
                ?
                <div className='product'>
                    {/* 返回店铺 */}
                    <div className='back-to-seller' onClick={this.goBack} style={{display: this.state.display_back}}>
                        <Icon type="left"/>
                        返回
                    </div>
                    <div className='capture'>

                        {/* 商品图片 */}
                        <div className='img-product'>
                            <Zmage src={`${baseUrl}${this.state.products[0].productDatas[0]}`} alt=''/>
                        </div>
                        {/* 描述和按钮 */}
                        <div>
                            <div className='price-and-pop'>
                                <span className='price'>¥{this.state.products[0].price}</span>
                                <span className='pop'>爆款</span>
                            </div>
                            <h2>{this.state.products[0].product_name}</h2>
                            <p className='infs'>{this.state.products[0].infs}</p>
                            <div className='share-shit' style={{display: this.state.display_name}}>
                                <QRCode value={window.location.href} size={64}/>
                                <div className='words'>
                                    <h2>小蓝色本有好东西</h2>
                                    <h4>长按保存图片或发送给好友</h4>
                                    <h4>扫描二维码可查看详情</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* 按钮组 */}
                    <div className='buttons-group' style={{display: this.state.display_back}}>
                        {/* 链接组 */}
                        {this.renderButtonShit(this.state.products[0].productUrl)}
                        {/* 分享按钮 */}
                        <button className='button-share' onClick={this.share}>
                            分享<Icon type="share-alt"/>
                        </button>
                    </div>
                </div>
                : <Spin></Spin>

        )

    }
}
