import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import IndexAbout from "./Index/index.js";
import MyCommunications from "./MyCommunications/index.js";


export default class About extends React.Component {
    render() {
        return (
            <div>
                <Switch>
                    <Redirect exact from="/about" to='/about/index' component={IndexAbout}/>
                    <Route path="/about/index" component={IndexAbout}/>
                    <Route  path="/about/communications" component={MyCommunications}/>
                </Switch>
                {/* 底部 */}
            </div>
        )
    }
}
