import React from 'react'
import {Link, Redirect, Route} from 'react-router-dom'
import {Avatar, Button, Icon, Modal} from 'antd';
import './index.less'
import MyFav from "./MyFav";
import TaskAcepted from "./TaskAcepted";

export default class IndexAbout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            avatarUrl: '',
            nickName: '',
            token: '',
            // 发布链接
            visiblePostLinks: false,
        }
    }

    showPostLinks = () => {
        this.setState({
            visiblePostLinks: true,
        });
    };
    handleOk = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    componentDidMount() {
        const cookie2dic = () => {
            var cookies = document.cookie.split("; ");
            var dic = [];
            for (let i = 0; i < cookies.length; i++) {
                const e = cookies[i];
                var key_shit = e.split("=")[0];
                var val = unescape(e.split("=")[1]);
                if (key_shit === 'nickName') {
                    val = decodeURI(e.split("=")[1])
                }
                dic[key_shit] = val
            }
            console.log('解析后的cookie:', dic);
            return dic
        };
        var dic_cookie = cookie2dic();
        // 设置信息
        this.setState({
            avatarUrl: '' || dic_cookie.avatarUrl,
            nickName: '' || dic_cookie.nickName,
            token: '' || dic_cookie.token,
        })


    }

    render() {
        return (
            <div className="About">
                {/* 用户名与头像 */}
                <div className='header'>
                    <Avatar size={64} src={this.state.avatarUrl}/>
                    <span className='user-name'>{this.state.nickName}</span>
                    {/*<Link to='/about/communications'>设置</Link>*/}
                </div>
                {/* 发布任务 */}
                <div className='user-actions'>
                    {/*<Link to='/admin/customer/upload/question'>*/}
                    <div className='post-new' onClick={this.showPostLinks}>
                        <Icon type="plus-circle" style={{fontSize: '30px'}}/>
                        <div>
                            发布新任务
                        </div>
                    </div>
                    {/*</Link>*/}
                </div>
                <Modal
                    visible={this.state.visiblePostLinks}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    <Link to='/admin/customer/upload/question/1'>
                        <Button type='primary'>代办事情</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/2'>
                        <Button type='primary'>转发集赞</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/3'>
                        <Button type='primary'>填写问卷</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/0'>
                        <Button type='primary'>其他任务</Button>
                    </Link>
                </Modal>
                <hr/>
                {/* 子路由导航 */}
                <div className='nav-route'>
                    <Link
                        to={`${this.props.match.url}/MyFav`}
                        className={document.location.hash.includes('MyFav') ? 'active' : 'unactive'}
                    >
                        <Icon type="star" style={{fontSize: '30px'}}/>
                        <div>
                            我的收藏
                        </div>

                    </Link>
                    <Link
                        to={`${this.props.match.url}/TaskPosted`}
                        className={document.location.hash.includes('TaskPosted') ? 'active' : 'unactive'}
                    >
                        <Icon type="carry-out" style={{fontSize: '30px'}}/>
                        <div>
                            我的发布
                        </div>
                    </Link>
                    <Link
                        to={`${this.props.match.url}/TaskAcepted`}
                        className={document.location.hash.includes('TaskAcepted') ? 'active' : 'unactive'}
                    >
                        <Icon type="container" style={{fontSize: '30px'}}/>
                        <div>
                            我的接单
                        </div>

                    </Link>
                </div>
                {/* 子路由 */}
                <div className='routes-container'>
                    <Route exact path={this.props.match.url} render={() => (
                        <Redirect to={`${this.props.match.url}/MyFav`}/>
                    )}/>
                    <Route exact path={`${this.props.match.url}/MyFav`} render={() => (
                        <MyFav/>
                    )}/>
                    <Route exact path={`${this.props.match.url}/TaskPosted`} render={() => (
                        <TaskPosted/>
                    )}/>
                    <Route exact path={`${this.props.match.url}/TaskAcepted`} render={() => (
                        <TaskAcepted/>
                    )}/>
                </div>
            </div>

        )
    }
}
const TaskPosted = () => {
    return (<div>我发布的任务</div>)
};